.nav_container nav{display:flex;align-items:center;justify-content:space-between;background:rgba(0,0,0,0);padding:40px 0px;position:sticky;top:0;z-index:9999}@media(max-width: 1200px){.nav_container nav{justify-content:center}}.nav_container nav.scrolling-nav{background-image:linear-gradient(to bottom, #fff, rgba(255, 255, 255, 0.999));background-color:rgba(0,0,0,0)}.nav_container nav .mobile_loginbtn{display:none}.nav_container nav #navbar{display:flex;align-items:center;justify-content:center}.nav_container nav #navbar li{list-style:none;padding:0 20px;position:relative}.nav_container nav #navbar li a{text-decoration:none;font-size:1.3rem;font-weight:500;color:#000;transition:.3s ease-in-out;font-family:"Poppins";font-style:normal;font-weight:500;font-size:18px;line-height:27px}.nav_container nav #navbar li a:hover,.nav_container nav #navbar li a.active{color:#fd5901}.nav_container nav #navbar li a:hover::after,.nav_container nav #navbar li a.active::after{content:"";width:40%;height:2px;background:#fd5901;position:absolute;bottom:-4px;left:30px}@media(min-width: 768px)and (max-width: 1200px){.nav_container nav .logo_hamberger{margin-bottom:20px}}.nav_container nav #mobile{display:none}.nav_container nav #mobile i{color:#fff;align-items:center}.nav_container nav #mobile img{width:2rem}.nav_container nav .loginbtn{border:2px solid var(--currentTheme);border-radius:50px;color:var(--currentTheme-text);text-align:center;font-family:"Poppins";font-style:normal;font-weight:500;font-size:12px;line-height:27px;display:flex;flex-direction:row;justify-content:center;align-items:center;padding:10px;gap:10px;min-width:155px;height:47px;left:1185px;top:73px;background:var(--currentTheme);border-radius:50px}.nav_container nav .loginbtn:hover::after{display:none}.nav_container nav .dropdown-menu{display:none;position:absolute;top:100%;left:0;background-color:#fff;box-shadow:0px 4px 6px rgba(0,0,0,.1)}.nav_container nav .dropdown-menu li{margin-bottom:.7rem}.nav_container nav .dropdown:hover .dropdown-menu{display:block}.nav_container nav .dropdown-label{transition:.3s ease-in-out;font-family:"Poppins";font-style:normal;font-weight:500;font-size:18px;line-height:27px}.nav_container nav .dropdown-label::after{content:" ▼";vertical-align:middle;font-size:.7em}@media screen and (max-width: 820px){.nav_container nav{padding:25px 0px}.nav_container nav .logo img{width:150px}.nav_container nav #navbar{display:flex;flex-direction:column;align-items:flex-start;justify-content:flex-start;position:fixed;top:90px;left:-300px;width:300px;height:100vh;background:#fff;box-shadow:0 40px 60px rgba(0,0,0,.1);padding:40px 0 0 10px;transition:.3s ease-in-out}.nav_container nav .mobile_loginbtn{border:2px solid var(--currentTheme);border-radius:50px;color:var(--currentTheme-text);text-align:center;font-family:"Poppins";font-style:normal;font-weight:500;font-size:15px;line-height:27px;display:flex;flex-direction:row;justify-content:center;align-items:center;padding:10px;gap:10px;width:100px;height:37px;left:1185px;top:73px;background:var(--currentTheme);border-radius:50px;text-decoration:none}.nav_container nav .mobile_loginbtn:hover::after{display:none}.nav_container nav #navbar.active{left:0px}.nav_container nav #navbar li{margin-bottom:25px}.nav_container nav .hamberger_logo_login_container{display:flex;justify-content:space-between;width:100%;align-items:center}.nav_container nav .hamberger_logo_login_container .logo_hamberger{display:flex}.nav_container nav .hamberger_logo_login_container .logo_hamberger a{margin-left:15px}.nav_container nav #mobile{display:block}.nav_container nav #mobile i{font-size:24px;cursor:pointer}.nav_container nav .loginbtn{display:none}}